.projects {
    min-height: 100vh;
    background-color:#010051;
    display: flex;
    justify-content: center;
}

.container {
    width: 80%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
/*    border: 1px solid black;*/
}

.header {
/*    border: 1px solid black;*/
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #DEABD4;
}

.blockProjects {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    margin: 0 0 30px 0;
   /* border: 1px solid #ffffff;*/
    flex-wrap: wrap;
    align-items: center;
}

.project{
 /*   border: 1px solid black;*/
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 360px;
    width: 380px;
    color: white;
    margin: 20px;
    background-color: rgba(1, 0, 127, 0.71);
}

.project h2{
    color: #DEABD4;
}

.project img {
    width: 250px;
    height: 250px;
    background-color: #696969;
    margin: 15px;
}

.description{
    padding: 15px;
}

@media screen and (max-width: 700px ) {
   .blockProjects{
       justify-content: space-evenly;
   }
    .project img {
        width: 180px;
        height: 180px;
    }
}

@media screen and (max-width: 400px ) {
    .project img {
        width: 140px;
        height: 140px;
    }
}

@media screen and (max-width: 430px ) {
    .project {
        min-height: 300px;
        width: 250px;
    }
}
