.footer {
    min-height: 300px;
    background-color: #ffe2ea;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header {
    color: #404040;
}

.copyright{
    color: #404040;
    text-align: center;
}

.container {
/*    border: 1px solid black;*/
    width: 80%;
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.socialNetworks {
    display: flex;
    width: 65%;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin: 20px;
}

.myPage img {
    width: 100px;
    height: 100px;
    background-color: #696969;
    margin: 10px;
}

@media screen and (max-width: 500px ) {
    .myPage img {
        width: 60px;
        height: 60px;
    }

}